import React from 'react';
import { useNavigate, useLocation } from "react-router-dom";
import "./SearchBox.css";
import "bootstrap/dist/css/bootstrap.min.css";
import Logo from "../assets/images/logo.png";

const Menu = ({brand}) => {
    const location = useLocation();
    const currentPath = location.pathname;

    const navigate = useNavigate();

    const contract = async (event) => {
        event.preventDefault();
    
        try {
        navigate("/contract", {
            state: {
            brand: brand
                },
            });
            } catch (error) {
                console.error("Error:", error);
            }
        };

    const mediumSearch = async (event) => {
        event.preventDefault();
    
        try {
        navigate("/customer", {
            state: {
            brand: brand
                },
            });
            } catch (error) {
                console.error("Error:", error);
            }
        };

    const shopSearch = async (event) =>{
        event.preventDefault();
        try {
            navigate("/shop", {
                state: {
                    brand: brand
                },
            });
        } catch (error) {
            console.error("Error:", error);
        }
    }

    const customerTrend = async() =>{
        navigate("/customerTrend", {
            state: {
            brand: brand
            },
        });
    }

    const shopTrend = async() =>{
        navigate("/shopTrend", {
            state: {
                brand: brand
            },
        });
    }

    const campaign = async() =>{
        navigate("/campaign",{
            state:{
                brand:brand
            },
        })
    }

    const list = async() =>{
        navigate("/list",{
            state:{
                brand:brand
            },
        })
    }

    const budget = async() =>{
        navigate("/budget",{
            state:{
                brand:brand
            },
        })
    }

    const budgetAccounting = async() =>{
        navigate("/budgetAccounting",{
            state:{
                brand:brand
            },
        })
    }

    const rank = async() =>{
        navigate("/rank",{
            state:{
                brand:brand
            },
        })
    }

    const loginLog = async() =>{
        navigate("/log",{
            state:{
                brand:brand
            },
        });
    };

    const calendar = async() =>{
        navigate("/calendar",{
            state:{
                brand: brand
            },
        });
    };

    const database = async() =>{
        navigate("/database",{
            state:{
                brand: brand
            },
        });
    };

    const home = async() =>{
        navigate("/");
    }



  return (
    <div className='d-flex mt-2'>
        <div className='menuLogo m-3' onClick={home}><img src={Logo} alt="PG-CLOUD" className='w-100'/></div>
        <div className="menu d-flex flex-wrap">
            <div className={`category btn me-1 mb-1 btn-large text-dark px-2 fw-bold ${currentPath === "/contract" ? "selected ": ""}`} onClick={currentPath === "/contract" ? null : contract}></div>
            <div className={`category btn me-1 mb-1 btn-large text-dark px-2 fw-bold ${currentPath === "/customer" ? "selected": ""}`} onClick={currentPath === "/customer" ? null : mediumSearch}></div>
            <div className={`category btn me-1 mb-1 btn-large text-dark px-2 fw-bold position-relative ${currentPath === "/rank" ? "selected": ""}`} onClick={currentPath === "/rank" ? null : rank}></div>
            <div className={`category btn me-1 mb-1 btn-large text-dark px-2 fw-bold position-relative ${currentPath === "/database" ? "selected": ""}`} onClick={currentPath === "/database" ? null : database}><div className='position-absolute accounting bg-primary text-white rounded px-1'>new</div></div>
            {/* <div className={`category btn me-1 mb-1 btn-large text-dark px-2 fw-bold position-relative ${currentPath === "/shop" ? "selected": ""}`} onClick={currentPath === "/shop" ? null : shopSearch}></div> */}
            <div className={`category btn me-1 mb-1 btn-large text-dark px-2 fw-bold ${currentPath === "/customerTrend" ? "selected": ""}`} onClick={currentPath === "/customerTrend" ? null : customerTrend}></div>
            <div className={`category btn me-1 mb-1 btn-large text-dark px-2 fw-bold ${currentPath === "/shopTrend" ? "selected": ""}`} onClick={currentPath === "/shopTrend" ? null : shopTrend}></div>
            <div className={`category btn me-1 mb-1  btn-large text-dark px-2 fw-bold position-relative ${currentPath === "/calendar" ? "selected": ""}`} onClick={currentPath === "/calendar" ? null : calendar}></div>
            <div className={`category btn me-1 mb-1 btn-large text-dark px-2 fw-bold position-relative ${currentPath === "/list" ? "selected": ""}`} onClick={currentPath === "/list" ? null : list}></div>
            {/* <div className={`category btn me-1 mb-1 btn-large text-dark px-2 fw-bold position-relative ${currentPath === "/list" ? "selected": ""}`} onClick={currentPath === "/list" ? null : list}><div className='position-absolute accounting bg-primary text-white rounded px-1'>6/1</div></div> */}
            {/* <div className={`category btn me-1 mb-1 btn-large text-dark px-2 fw-bold position-relative ${currentPath === "/database" ? "selected": ""}`} onClick={currentPath === "/database" ? null : database}>PG CLOUD</div> */}
            { brand === "BrandAdmin" || brand === "Master" ? <div className={`category btn me-1 mb-1 btn-large text-dark px-2 fw-bold position-relative ${currentPath === "/campaign" ? "selected": ""}`} onClick={currentPath === "/campaign" ? null : campaign}><div className='position-absolute accounting bg-primary text-white rounded px-1'></div></div> : null}
            { brand === "BrandAdmin" || brand === "Master" ? <div className={`category btn me-1 mb-1 btn-large text-dark px-2 fw-bold position-relative ${currentPath === "/budget" ? "selected": ""}`} onClick={currentPath === "/budget" ? null : budget}><div className='position-absolute accounting bg-primary text-white rounded px-1'></div></div> : null}
            { brand === "BrandAdmin" || brand === "Master" ? <div className={`category btn me-1 mb-1 btn-large text-dark px-2 fw-bold position-relative ${currentPath === "/budgetAccounting" ? "selected": ""}`} onClick={currentPath === "/budgetAccounting" ? null : budgetAccounting}><div className='position-absolute accounting bg-danger text-white rounded px-1'></div></div> : null}
            { brand === "BrandAdmin" || brand === "Master" ? <div className={`category btn me-1 mb-1 btn-large text-dark px-2 fw-bold position-relative ${currentPath === "/log" ? "selected": ""}`} onClick={currentPath === "/log" ? null : loginLog}><div className='position-absolute accounting bg-primary text-white rounded px-1'></div></div> : null}
        </div>
    </div>
  )
}

export default Menu